.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 70%;
  transform: translate(15%);
  padding: 4rem 0rem;
  align-items: center;
  margin: 10rem 4rem;
  border: 1px solid #cecfce;
  text-align: start;
}

.title {
  font-size: 1.5rem;
  position: relative;
  bottom: 20px;
  right: 10rem;
}

.addComment {
  display: flex;
  flex-direction: column;
  position: relative;
  bottom: 20px;
  right: 0.6rem;
  top: 0rem;
}

.addComment textarea {
  padding: 1rem 1rem;
  width: 30rem;
  height: 10rem;
}

.addComment button {
  padding: 1rem;
  margin-top: 2rem;
}

@media screen and (max-width: 850px) {
  .container {
    transform: translate(-13%);
    width: 85%;
  }
  .title {
    font-size: 1rem;
    right: 3.8rem;
  }
  .addComment textarea {
    padding: 1rem 1rem;
    width: 14rem;
    height: 7rem;
  }
}
